import {Component, ComponentInterface, h, Host, Prop} from '@stencil/core';

/**
 * The bq-grid component provides a responsive grid layout system.
 *
 * @example Usage
 * ```html
 * <bq-grid fixed no-padding>Content</bq-grid>
 * ```
 *
 * @status progress
 * @slot - Content to display inside the grid.
 * @cssprop --bq-grid--margin - Margin for the grid container.
 */
@Component({
    tag: 'bq-grid',
    styleUrl: './scss/bq-grid.scss',
    shadow: true,
})
export class BqGrid implements ComponentInterface {
    @Prop() fixed = false;

    @Prop() noPadding = false;

    render() {
        return (
            <Host
                class={{
                    'bq-grid': true,
                    'fixed': this.fixed,
                    'no-padding': this.noPadding,
                }}
            >
                <slot></slot>
            </Host>
        );
    }
}
