/* -------------------------------------------------------------------------- */
/*                        Grid styles                        */
/* -------------------------------------------------------------------------- */

@import './bq-grid.variables';

/* bq-grid.css */
:host {
  display: block;
  flex: 1;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  padding: 0.25rem; /* Default padding */

  @media (min-width: 640px) { /* sm */
    padding: 0.5rem;
  }

  @media (min-width: 768px) { /* md */
    padding: 1rem;
  }

  @media (min-width: 1024px) { /* lg */
    padding: 1.5rem;
  }

  @media (min-width: 1280px) { /* xl */
    padding: 2rem;
  }
}

:host.fixed {
  width: 100%; /* xs */
  @media (min-width: 640px) { /* sm */
    width: 540px;
  }
  @media (min-width: 768px) { /* md */
    width: 720px;
  }
  @media (min-width: 1024px) { /* lg */
    width: 960px;
  }
  @media (min-width: 1280px) { /* xl */
    width: 1140px;
  }
}

:host.no-padding {
  @apply p-0;
}